import Tool from './Tool'
//
let { Rectangle } = L;
/**
 * 
 */
export default class RectTool extends Tool {
  options = { feature: { color: 'red', fillOpacity: .5, weight: 1 } }
  /**
   * 
   */
  start() {
    this.initMouseDownHandler();
    let { map } = this;
    map.once('mousedown', this.onMouseDown, this);
  }
  /**
   * 
   */
  cancel() {
    let { map } = this;
    map.off('mousedown', this.onMouseDown, this);
    map.off('mousemove', this.onMouseMove, this);
    this.sp = null;
  }
  /**
   * 
   * @param e 
   */
  _onMouseDown(e) {
    let { map, layer, options } = this;
    let { latlng } = e;
    this.clear();//
    //
    this.marker = new Rectangle(L.latLngBounds(latlng, latlng), options.feature).addTo(layer)
    this.sp = latlng
    map.on('mousemove', this.onMouseMove, this);
    map.once('mouseup', this.onEnd, this);
  }
  /**
   * 
   * @param e 
   */
  onMouseMove(e) {
    let { marker } = this;
    let { latlng } = e;
    marker.setBounds(L.latLngBounds(this.sp, latlng))
    marker.redraw();
  }
}


