// import {distance} from './Util'
/**
 * n
 * @param arr [[x,y],[x,y]]
 */
export var bezier = (arr, step) => {
  let points = [arr[0]];//
  let l = arr.length;
  for (let i = step; i < 1; i += step) {
    points.push(PBezier(arr, l, i));
  }
  points.push(arr[l - 1]);
  return points;
}
/** 
 * 
 * @param arr
 * @param step
 * @param options
 */
export var coattail = (arr, step, options) => {
  let { r, ra, ma } = T.assign({
    r: 36,     //
    ra: 0.2,   //
    ma: 16     //
  }, options);
  let { PI } = Math;
  //
  let points = [arr[0]];// 
  let l = arr.length; //
  let p0 = arr[0]; // 
  let num = Math.ceil((1 / step) + 1) * 2;// 
  let ci = 1, p;
  if (l < 2) return arr;
  // 
  for (let i = step; i < 1; i += step, ci++) {
    p = PBezier(arr, l, i);//
    let r = ma - i * 9;    //
    let radians = Math.atan2(p[1] - p0[1], p[0] - p0[0]) - Math.PI / 2;
    let offsetX = r * Math.cos(radians);
    let offsetY = r * Math.sin(radians);
    points[ci] = [p[0] + offsetX, p[1] + offsetY];
    points[num - ci] = [p[0] - offsetX, p[1] - offsetY];
    p0 = p;
  }
  // 
  p = arr[l - 1];
  let radians = Math.atan2(p[1] - p0[1], p[0] - p0[0]);
  p = [p0[0] + (ma + 2) * Math.cos(radians), p0[1] + (ma + 2) * Math.sin(radians)];
  points[ci++] = [p[0] + r * Math.cos(PI * (1 + ra) + radians), p[1] + r * Math.sin(PI * (1 + ra) + radians)]
  points[ci++] = p;
  points[ci] = [p[0] + r * Math.cos(PI * (1 - ra) + radians), p[1] + r * Math.sin(PI * (1 - ra) + radians)]
  //  
  points.push(arr[0]);
  return points;
}
//
let PBezier = (arr, l, t) => {
  let px = 0,
    py = 0,
    n = l - 1;//n
  for (let i = 0, b; i < l; i++) {
    b = BEZ(n, i, t);
    px += arr[i][0] * b;
    py += arr[i][1] * b;
  }
  return [px, py]
}
//
let BEZ = (n, k, t) => {
  return C(n, k) * Math.pow(t, k) * Math.pow(1 - t, n - k);
}
//
let C = (n, k) => {
  return Factorial(n) / (Factorial(k) * Factorial(n - k));
}
//
let Factorial = i => {
  let n = 1;
  for (let j = 1; j <= i; j++) {
    n *= j;
  }
  return n;
}


