/**
 * @author gyb(mocheer)
 * @since 2016.11.4
 * 
 */
import lonlats from '../utils/LonLats';
import Quadrant from '../feature/Quadrant';
import Popup from '../polyfill/Popup';
import VectorMarker from './VectorMarker'
let {
  marker: toMarker,
  canvas: toCanvas,
  polyline: toPolyline,
  circle: toCircle,
  circleMarker,
  divIcon: toDivIcon,
  tooltip: toTooltip,
  featureGroup: toFeatureGroup,
  latLng: toLatLng
} = L;
let {
  assign: $assign
} = T;

// svg[Attributes]{
//   stroke - linejoin: round;
//   stroke - linecap: round;
//   pointer - events: none;
//   stroke: rgb(0, 150, 255);
//   stroke - opacity: 0.8;
//   stroke - width: 3;
//   fill: none;
// }

/**
 * 
 * @class Typhoon
 * todo:  FeatureGroup 
 */
export default class Typhoon {
  /**
   * 
   */
  options = {
    // 
    autoPlay: true,
    // 
    hasLsljLater: true,
    // 
    inView: true,
    // inview=truekeepZoom=true,
    keepZoom: true,
    // 
    click2drawOnTime: true,
    // 
    hitForPoint: true,
    // 
    ls: {
      color: '#0096FF',
      weight: 3,
      opacity: .8,
      interactive: false
    },
    // -
    lsyb: {
      color: '#666666',
      weight: 2,
      opacity: .8,
      interactive: false
    },
    // ,
    org: {
      weight: 1,
      dashArray: '10,3',
      interactive: false
    },
    orgs: [{
        label: "",
        th: "CN",
        color: "#FF4050"
      },
      {
        label: "",
        th: "KR",
        color: "#000000"
      },
      {
        label: "",
        th: "US",
        color: "#4099EE"
      },
      {
        label: "",
        th: "TW",
        color: "#FFA040"
      },
      {
        label: "",
        th: "JP",
        color: "#43FF4B"
      },
      {
        label: "",
        th: "HK",
        color: "#FF40F5"
      }
    ],
    // 
    grade: {
      color: '#000',
      radius: 4,
      fillColor: '#FFF',
      weight: 1,
      fillOpacity: 1,
      src: 'circle'
    },
    grades: [{
        F1: 0,
        F2: 0.2
      },
      {
        F1: 0.3,
        F2: 1.5
      },
      {
        F1: 1.6,
        F2: 3.3
      },
      {
        F1: 3.4,
        F2: 5.4
      },
      {
        F1: 5.5,
        F2: 7.9
      },
      {
        F1: 8,
        F2: 10.7
      },
      {
        F1: 10.8,
        F2: 13.8,
        fillColor: '#00FF03',
        GRADENAME: ''
      },
      {
        F1: 13.95,
        F2: 17.15,
        fillColor: '#00FF03',
        GRADENAME: ''
      },
      {
        F1: 17.2,
        F2: 20.7,
        fillColor: '#0062FE',
        GRADENAME: ''
      },
      {
        F1: 20.8,
        F2: 24.4,
        fillColor: '#0062FE',
        GRADENAME: ''
      },
      {
        F1: 24.5,
        F2: 28.4,
        fillColor: '#FDFA00',
        GRADENAME: ''
      },
      {
        F1: 28.5,
        F2: 32.6,
        fillColor: '#FDFA00',
        GRADENAME: ''
      },
      {
        F1: 32.7,
        F2: 36.9,
        fillColor: '#FDAC03',
        GRADENAME: ''
      },
      {
        F1: 37,
        F2: 41.4,
        fillColor: '#FDAC03',
        GRADENAME: ''
      },
      {
        F1: 41.5,
        F2: 46.1,
        fillColor: '#F072F6',
        GRADENAME: ''
      },
      {
        F1: 46.2,
        F2: 50.9,
        fillColor: '#F072F6',
        GRADENAME: ''
      },
      {
        F1: 51,
        F2: 56,
        fillColor: '#FD0002',
        GRADENAME: ''
      },
      {
        F1: 56.1,
        F2: 161.2,
        fillColor: '#FD0002',
        GRADENAME: ''
      }
    ],
    // 
    c7: {
      fillColor: '#79C0AE',
      fillOpacity: 0.5,
      color: '#49BA6E',
      weight: 1
    },
    c10: {
      fillColor: '#FFFF00',
      fillOpacity: 0.2,
      color: '#FFFF00',
      weight: 1
    },
    c12: {
      fillColor: '#FF0000',
      fillOpacity: 0.3,
      color: '#FF0000',
      weight: 1
    },
    // 
    land: {
      src: T + 'assets/common/dmap/markers/flag.png'
    }
    // 
    // hurricane: {iconUrl: T + "assets/common/dmap/markers/typhoonIco.gif",iconSize: [32, 32]}
    // 
  }
  // 
  group = toFeatureGroup();
  //
  /**
   * 
   * @param {*} data 
   * @param {*} style 
   */
  constructor(data, options) {
    this.data = T.action(data);
    options = $assign(this.options, options);
    options.grades = options.grades.map(val => $assign({}, options.grade, val));
    //
    this._points = [];
  }
  /**
   * 
   * @param {*} options 
   */
  setOptions(options) {
    $assign(this.options, options);
    this.update();
  }
  /**
   * 
   */
  update(data) {
    if (data && data !== this.data) { //
      this.data = data;
      this.ci = data.points.length - 1;
    }
    if (this.complete) {
      this.draw(this.ci);
    }
  }
  /**
   *  
   * @param coor
   * @param options
   * @param data
   */
  createPoint(coor, options, data) {
    let marker = circleMarker(lonlats(coor), options);
    data._source = this.data;
    marker.__data = data;
    // 
    options = this.options.popup;
    options && marker.bindPopup(new Popup(options), options);
    return marker;
  }
  /**
   * 
   */
  createLand(coor, options) {
    return new VectorMarker(lonlats(coor), options);
  }
  /**
   * 
   * @param coor
   * @param options
   */
  createPolyline(coor, options) {
    return toPolyline(lonlats(coor, 1), options);
  }
  /**
   * 
   * @param center
   * @param radius
   * @param options
   */
  createCicle(center, radius, options) {
    return radius && toCircle(lonlats(center), radius * 1000, options);
  }
  /**
   * 
   * { ne, nw, sw, se}-> [ne, nw, sw, se]
   * @param center
   * @param radius
   * @param style
   */
  createQuadrant(center, radius, style) {
    if (!radius) return;
    let {
      ne,
      nw,
      sw,
      se
    } = radius;
    if (!ne || !nw || !sw || !se) {
      return;
    }
    this.renderer = this.renderer || toCanvas({
      style: {
        zIndex: 40
      }
    });
    return new Quadrant([center[1], center[0]], $assign({}, style, {
      renderer: this.renderer,
      radius: [ne * 1000, nw * 1000, sw * 1000, se * 1000]
    }))
  }
  /**
   * 
   * @param center
   * @param options
   */
  createHurricane(center, options, grade) {
    let colorIndex = parseInt((grade - 6) / 2) + 1;
    return options ? toMarker(lonlats(center), {
      icon: toDivIcon(options)
    }) : new VectorMarker(lonlats(center), {
      src: T + `assets/common/dmap/typhoon/hurricane${colorIndex > 0 ? colorIndex : 0}.png`,
      rotate: true
    })
  }
  /**
   * draw
   * {points}
   * {radius7,radius10,radius12,radius7_quad,radius10_quad,radius12_quad}-> {r7,r10,r12,r7_q,r10_q,r12_q}
   * {lng,lat} -> {lon,lat}
   * {power,strong} ,
   * {sets,points} 
   * @param time
   * @property _points 
   */
  draw(time) {
    let that = this;
    let {
      options,
      data,
      group,
      lsp,
      createPolyline,
      createCicle,
      _points
    } = that;
    let {
      autoPlay,
      ls,
      lsyb,
      grades,
      c7,
      c10,
      c12,
      org,
      orgs,
      hurricane,
      hasLsljLater,
      hitForPoint
    } = options;
    let createPoint = that.createPoint.bind(that);
    let createLand = that.createLand.bind(that);
    let createQuadrant = that.createQuadrant.bind(that);
    let handleEvent = that.handleEvent.bind(that);
    let {
      points,
      land
    } = data;
    let ci = this.getTimeIndex(points, time); //
    let len = points.length;
    let addLayer = (value, index, array) => {
      if (!value || (array === lsp && index > ci && autoPlay)) return;
      group.addLayer(value);
    }
    let getDistance = (p1, p2) => Math.sqrt((p1.x - p2.x) * (p1.x - p2.x) + (p1.y - p2.y) * (p1.y - p2.y));
    if (!that.tempLayer) {
      that.tempLayer = toFeatureGroup();
      group.addLayer(that.tempLayer);
    }
    let {
      tempLayer
    } = that;
    let addTempLayer = (value, index, array) => {
      if (!value || (array === lsp && index > ci && autoPlay)) return;
      tempLayer.addLayer(value);
    }
    //
    let drawing = (i, prev) => {
      // Popup
      tempLayer.clearLayers();
      if (!i || i < prev) {
        group.clearLayers();
        group.addLayer(tempLayer);
      }
      // 
      let lslj; // 
      let lslj_yb; // -
      let yblj = []; // 
      let tffq = []; // 
      let landPoints = []; // 
      // 
      let item = points[i];
      //
      let lslj_prev = points.slice(0, i + 1);
      let lslj_after = points.slice(i);
      let coor = [item.lng, item.lat];
      // ""
      if (lslj_prev.length > 1) {
        lslj = createPolyline(lslj_prev, ls);
      }
      // ""-
      if (!autoPlay && hasLsljLater && lslj_after.length > 1) {
        lslj_yb = createPolyline(lslj_after, lsyb);
        lslj_prev = lslj_prev.concat(lslj_after.slice(1));
      }
      //
      lsp.length = lslj_prev.length;
      // 
      lslj_prev.forEach((item, i) => {
        // 
        if (hitForPoint) {
          // 
          let point = item._point = this.map.latLngToLayerPoint(toLatLng(item));
          // 
          let point_prev = (i => {
            while (i--) {
              if (lsp[i]) return lslj_prev[i]._point;
            }
          })(i);
          if (i > 0 && getDistance(point, point_prev) < 8) {
            lsp[i] = null;
            return;
          }
        }
        // 
        let marker = lsp[i] = _points[i] = _points[i] || createPoint([item.lng, item.lat], grades[item.power || 0], item).on('click mouseover mouseout', handleEvent)
        if (i === 0 && !marker.getTooltip()) {
          let tooltip = toTooltip(T.merge({
            zoom: 1,
            direction: 'right',
            offset: [5, 0],
          }, this.options.tooltip), marker).setContent(data.tfbh + data.name);
          marker.bindTooltip(tooltip);
        }
      })
      // 
      landPoints = options.land && (land || []).map(data => {
        let tm = T.clock(data.land_time);
        let ctm = T.clock(item.time);
        if (tm.date < ctm.date) {
          let marker = createLand(data, options.land)
          return marker;
        }
      })

      // 
      if (autoPlay || i === ci) { //
        let {
          radius7_quad,
          radius10_quad,
          radius12_quad,
          forecast
        } = item;
        // 
        let h = that.createHurricane(coor, hurricane, item.power);
        let r7 = createQuadrant(coor, radius7_quad, c7) || createCicle(coor, item.radius7, c7);
        let r10 = createQuadrant(coor, radius10_quad, c10) || createCicle(coor, item.radius10, c10);
        let r12 = createQuadrant(coor, radius12_quad, c12) || createCicle(coor, item.radius12, c12);
        tffq.push(r7, r10, r12, h);
        //  
        if (!autoPlay || i === len - 1) {
          let _points = [];
          orgs.forEach(data => {
            let sets = data.label; //
            let index = i;
            let item;
            let point;
            let search = {
              sets: sets
            };
            while (!item && index >= 0) {
              point = points[index--];
              forecast = point.forecast;
              item = forecast && T.find(forecast, search);
            }
            if (item) {
              let {
                sets,
                points
              } = item;
              let style = $assign({}, org, T.find(orgs, {
                label: sets
              }));
              yblj.push(createPolyline([point].concat(points), style));
              //
              points.forEach((item) => {
                item.sets = sets; // 
                // 
                let point = item._point = this.map.latLngToLayerPoint(toLatLng(item));
                if (hitForPoint) {
                  for (let j = 0, len = _points.length; j < len; j++) {
                    if (getDistance(point, _points[j]) < 8) {
                      return;
                    }
                  }
                }
                _points.push(point);
                //
                yblj.push(createPoint(item, grades[item.power || 0], item).on('click mouseover mouseout', handleEvent))
                //
              })
            }
          })
        }
      }
      //
      tffq.forEach(addTempLayer);
      yblj.forEach(addTempLayer);
      addTempLayer(lslj);
      addTempLayer(lslj_yb);
      lsp.forEach(addLayer);
      landPoints.forEach(addTempLayer);
      tempLayer.bringToBack();
    }

    if (autoPlay) {
      let i = 0;
      let playing = () => {
        //init 
        if (this.init) {
          if (i < len && that.layer) {
            drawing(i, i - 1);
            setTimeout(playing, 30);
            i++;
          } else if (that.layer) {
            T.do('typhoon-playend', {
              tf: data,
              attr: points[i - 1]
            })
            options.autoPlay = false;
            that.complete = true;
          }
          // 
          if ((i === len) && options.inView && !this.layer.suspended) {
            (this.map && options.keepZoom) ? this.map.setView(points[i - 1]): this.map.fitBounds(toPolyline([
              [30, 105],
              [10, 125]
            ].concat(points)).getBounds());
          }
        }
      }
      playing();
    } else {
      drawing(ci, that.ci);
      T.do('typhoon-playend', {
        tf: data,
        attr: points[ci]
      })
      that.complete = true;
    }
    that.ci = ci;
  }

  /**
   * 
   * @param data 
   * @param time 
   */
  getTimeIndex(data, time) {
    if (time !== void 0) {
      return +time;
    }
    return data.length - 1;
  }
  /**
   * 
   * @param time
   */
  setTimeIndex(time) {
    if (this.ci !== time) {
      if (this.complete) {
        // 
        this.data.do('setTimeIndex', time)
        this.draw(time);
      }
    }
  }
  /**
   * 
   * @param layer 
   */
  addTo(layer) {
    let that = this;
    if (layer.map) {
      that.map = layer.map.on('zoomend', () => {
        that.init && that.update()
      });
      that.layer = layer;
      layer._add(this.group);
      if (!that.init) {
        that.init = true;
        that.lsp = [];
        that.draw();
      }
    } else {
      //  
      layer.layer.on('add', () => {
        this.addTo(layer);
      })
    }
  }
  /**
   * 
   */
  remove() {
    this.init = false;
    //  removelayer
    //  removeFrom
    this.layer && this.group.removeFrom(this.layer.layer);
  }
  /**
   * /
   * @param e
   */
  handleEvent(e) {
    let {
      data,
      lsp,
      complete,
      _overTarget,
      options
    } = this;
    let {
      target,
      type
    } = e;
    let i = lsp.indexOf(target);
    if (!complete) {
      return;
    }
    let event;
    switch (type) {
      case 'click':
        event = {
          data: data,
          dataProvider: data.points,
          selItem: data.points[i],
          event: e
        };
        if (lsp[i]) {
          options.click2drawOnTime && this.setTimeIndex(i);
          target.openPopup();
          target.setRadius(7);
          T.do('typhoon-lslj-click', event);
          //
        } else {
          target.openPopup();
          T.do('typhoon-yblj-click', event);
        }
        break;
      case 'mouseover':
        event = {
          tf: data,
          attr: target.__data || data.points[i],
          event: e
        };
        if (lsp[i]) {
          if (_overTarget != target) {
            target.setRadius(7);
            this._overTarget = target;
          }
          T.do('typhoon-lslj-over', event);
        } else {
          T.do('typhoon-yblj-over', event);
        }
        break;
      case 'mouseout':
        if (_overTarget) {
          _overTarget.setRadius(4);
          this._overTarget = null;
        }
        T.do('typhoon-lslj-out', event);
        break;
    }
  }
}



