let { Circle } = L;
/**
 * 
 */
Circle.include({
  /**
   * 
   */
  toGeoJSON() {
    let { lat, lng } = this.getLatLng();
    this.options.radius = this._mRadius;
    return {
      type: 'Feature',
      properties: {},
      geometry: { coordinates: [lng, lat], type: 'Circle' }
    }
  }
})
/**
 * 
 */
export default Circle.extend({
  /**
   * L.Circle
   * 
   */
  _updateBounds: function () {
    this._radius = this._radiusY;
    Circle.prototype._updateBounds.call(this);
  }
})



