import PointTool from './PointTool'

//
/**
 * 
 */
export default class TextTool extends PointTool {
  options = { feature: {} }
  /**
   * -start
   * @param e 
   */
  _onMouseDown(e) {
    let { latlng } = e;
    let { options, layer } = this;
    //
    this.marker = L.text(latlng, options.text, options.feature).addTo(layer)
    this.onEnd();
  }
}


