/**
 * @author gyb(mocheer)
 * @email `mocheer@foxmail.com`  
 * @since 2018-3-23
 * @see https://github.com/yakitoritabetai/Leaflet.LabelTextCollision
 * @see https://github.com/XC-Zhang/leaflet-text
 */
let { Layer } = L;
/** 
 * 
 */
Layer.include({
  /**
   * 
   */
  getText() {
    return this._text;
  },
  /**
   * 
   * @param {*} map 
   */
  bindText(content, options) {
    //
    if (this._text instanceof L.Text) {
      this._text.setContent(content);
    } else {
      let { _latlng } = this;
      // 
      this._text = content ? L.text(_latlng, content, options) : new L.VectorMarker(_latlng, options);
    }
    //
    this._initTextInteractions();
    //
    if (this._map && this._map.hasLayer(this)) {
      this.openText();
    }
    return this;
  },
  /**
   * 
   * @param 
   */
  _initTextInteractions: function () {
    this.on({
      remove: this._closeText,
      move: this._moveText
    });
  },
  /**
   * 
   * @param {*} layer 
   * @param {*} latlng 
   */
  openText(layer, latlng) {
    if (!(layer instanceof Layer)) {
      latlng = layer;
      layer = this;
    }
    if (!latlng) {
      latlng = layer.getCenter ? layer.getCenter() : layer.getLatLng();
    }
    let { _map, _text } = this;
    if (_map) {
      _text._source = layer;
      _text.setLatLng(latlng);
      if (!_map.hasLayer(_text)) {
        _map.addLayer(_text);
      }
    }
  },
  /**
   * 
   * @param {*} map 
   */
  unbindText(map) {
    let { _text } = this;
    if (_text) {
      map.removeLayer(_text);
      this._text = null;
    }
  },
  /**
   * 
   */
  _closeText() {
    let { _map, _text } = this;
    if (_map && _text && _map.hasLayer(_text)) {
      _map.removeLayer(_text);
    }
  },
  /**
   * 
   */
  _moveText(e) {
    this._text.setLatLng(e.latlng);
  }
})
/**
 * 
 */
L.Canvas.include({
  /**
   * 
   */
  _getTextWidth(layer) {
    this._ctx.font = layer.options.font;
    return this._ctx.measureText(layer._text).width;
  },
  /**
   * 
   */
  _updateText(layer) {
    if (!this._drawing || layer._empty()) {
      return;
    }

    var p = layer._point;
    var ctx = this._ctx;
    var options = layer.options;

    if (!options.fill) {
      return;
    }

    if (options.offset) {
      p = p.add(options.offset);
    }

    this._drawnLayers[layer._leaflet_id] = layer;
    ctx.font = options.font;
    ctx.translate(p.x, p.y);
    ctx.rotate(options.rotation);
    ctx.fillStyle = options.color;
    ctx.textAlign = options.textAlign;
    ctx.fillText(layer._text, 0, 0);
    ctx.rotate(-options.rotation);
    ctx.translate(-p.x, -p.y);
  }
});

/**
 * 
 */
L.Text = L.Path.extend({
  options: {
    color: 'blue',
    fill: true,
    fillOpacity: 1.0,
    rotation: 0.0,
    stroke: false,
    textAlign: 'center',
    font: "14px arial"
  },
  /**
   * 
   */
  initialize: function (latlng, text, options) {
    L.setOptions(this, options);
    this._latlng = L.latLng(latlng);
    this._text = String(text || options.text);
  },
  /**
   * 
   * @param {*} text 
   */
  setContent(text) {
    this._text = String(text);
    this._update();
  },

  setLatLng(latlng) {
    this._latlng = L.latLng(latlng);
    this._update();
  },

  getLatLng() {
    return this._latlng;
  },

  _project() {
    this._point = this._map.latLngToLayerPoint(this._latlng);
    this._updateBounds();
  },

  _updateBounds() {
    var w = this._renderer._getTextWidth(this);
    var p = [w / 2, w / 2];
    this._pxBounds = L.bounds(this._point.subtract(p), this._point.add(p));
  },

  _update() {
    if (this._map) {
      this._updatePath();
    }
  },

  _updatePath() {
    this._renderer._updateText(this);
  },

  _empty() {
    return !this._renderer._bounds.intersects(this._pxBounds);
  },

  _containsPoint: function (p) {
    return false;
  },
  /**
   * 
   */
  toGeoJSON() {
    let { lat, lng } = this.getLatLng();
    this.options.text = this._text;

    return {
      type: 'Feature',
      properties: {},
      geometry: { coordinates: [lng, lat], type: 'Text' }
    }
  }
});

L.text = function (latlng, text, options) {
  return new L.Text(latlng, text, options);
};


