import Tool from './Tool'
//
let { Polyline } = L;
/**
 * 
 */
export default class PolylineTool extends Tool {
  options = { feature: { color: 'red', weight: 2 } }

  /**
   * 
   */
  start() {
    let { map } = this;
    map.on('mousedown', this.onMouseDown, this);
    //
    this.restart = true;
  }
  /**
   * 
   */
  cancel() {
    let { map } = this;
    map.off('mousedown', this.onMouseDown, this);
    map.off('mousemove', this.onMouseMove, this);
    this.mp = null
  }
  /**
   * 
   */
  onMouseDown(e) {
    this.reset();
    //
    let { marker, map, layer, options } = this;
    let { latlng } = e;
    //
    if (!marker) {
      marker = this.marker = new Polyline([latlng], options.feature).addTo(layer)
      map.on('mousemove', this.onMouseMove, this);
      map.once('dblclick', this.onEnd, this)
    } else {
      this.mp = null
    }
  }
  /**
   * 
   * @param e 
   */
  onMouseMove(e) {
    let { marker, mp } = this;
    let { latlng } = e;
    if (!mp) {
      //
      marker.addLatLng(latlng);
      this.mp = latlng;
    } else {
      mp.lat = latlng.lat;
      mp.lng = latlng.lng;
      marker.getBounds().extend(mp)
      marker.redraw();
    }
  }

}


