import CircleTool from './CircleTool';
import Ellipse from '@/feature/Ellipse';

/**
 * 
 */
export default class EllipseTool extends CircleTool {
  /**
   * 
   * @param e 
   */
  _onMouseDown(e) {
    let { options } = this;
    let { map, layer } = this;
    let { latlng } = e;
    this.clear();//
    //
    this.marker = new Ellipse(latlng, 0, options.feature).addTo(layer);
    map.on('mousemove', this.onMouseMove, this);
    map.once('mouseup', this.onEnd, this);
  }

  /**
   * 
   * @param e 
   */
  onMouseMove(e) {
    let { marker } = this;
    let latlng = e.latlng;
    let center = marker.getLatLng();
    let latlng1 = latlng.clone();
    let latlng2 = latlng.clone();
    latlng1.lat = center.lat;
    latlng2.lng = center.lng;
    let r = latlng1.distanceTo(center);
    let ry = latlng2.distanceTo(center);
    //
    marker.setRadius(r);
    marker.setRadiusY(ry);
    marker.redraw();
  }

}



