/**
 * @author gyb(mocheer)
 * @email `mocheer@foxmail.com`  
 * @since 2017.6.12
 */
import PointTool from './tools/PointTool';
import PolylineTool from './tools/PolylineTool';
import { DistancePolylineTool, DistancePolygonTool } from './tools/DistanceTool';
import PenTool from './tools/PenTool';
import PenFillTool from './tools/PenFillTool';
import RectTool from './tools/RectTool';
import PolygonTool from './tools/PolygonTool';
import CircleTool from './tools/CircleTool';
import EllipseTool from './tools/EllipseTool';
import MilitaryArrowTool from './tools/MilitaryArrowTool';
import TextTool from "./tools/TextTool";
import VectorMarker from '@/feature/VectorMarker';
import FeatureLayer from "@/layer/FeatureLayer";
import { pointInPolygon } from '@/utils/geoUtils';
//
// Polyline  onAdd  flow 
import '@/plugins/meature/leaflet-measure-path';
import '@/plugins/meature/leaflet-measure-path.css';
//
let { isArray } = T;
let {
  Marker,
  Polyline,
  Polygon,
  Circle,
  CircleMarker
} = L;
/**
 * -
 */
export default class DrawLayer extends FeatureLayer {
  /**
   * 
   */
  history = []
  /**
   * 
   */
  next = []
  /**
   * 
   */
  marker
  /**
   * 
   */
  tool
  /**
   * 
   */
  Tool
  /**
   * 
   */
  // options = { reable: false, editable: true, draggable: true }
  /**
   * 
   * @param dmap 
   */
  addTo(dmap) {
    dmap.on('draw-end', this.onDrawEnd.bind(this));
    return super.addTo(dmap);
  }
  /**
   * 
   * @param {*} marker 
   * @param {*} options 
   * @param {*} data 
   */
  add(marker, options, data) {
    //
    if (options.tooltip) {
      options.tooltip.draggable = true;
    }

    super.add(marker, options, data);
    this._check(marker)
    //
  }
  /**
   *  marker 
   */
  _check(marker) {
    if (marker) {
      // 
      marker.editEnable();
      // 
      marker.makeDraggable();
      marker.dragging.enable();
    }
  }
  /**
   * 
   * @param options {tool,feature,tooltip,popup,onDrawEnd}
   */
  change(options) {
    let that = this;
    if (options) {
      T.data.pan = false;
      let { dmap, history, next } = that;
      switch (options.tool) {
        case 'pan':
          that.reset();
          dmap.el.style.cursor = "-webkit-grab";//grab
          T.data.pan = true;
          break;
        case 'clear'://break;
          that.clear();
        case 'pointer':
          that.reset();
          dmap.el.style.cursor = `default`;
          break;
        case 'go':  // 
          let todo = next[next.length - 1];
          if (todo && todo.restore()) {
            next.pop();
          }
          !history.includes(todo) && history.push(todo);
          that.reset();
          break;
        case 'back':// 
          let done = history[history.length - 1];
          if (done && done.reback()) {
            history.pop();
          }
          !next.includes(done) && next.push(done);
          that.reset();
          break;
        default:
          that.start(options);
          break;
      }
      dmap.do('tool-change', { target: that, options: options });
    }
    return that;
  }
  /**
   * 
   * @param options {tool:'',feature,tooltip,popup}
   */
  start(options) {
    let that = this;
    let { map } = that;
    let type = options.tool;
    that.cancel();
    map.dragging.disable();
    let Tool = that.get(type);
    let tool = Tool.create(that, options);
    // if (tool) {
    //   let mergeOptions = T.assign({}, that.options, options);
    //   let toolOptions = tool.options;
    //   toolOptions.feature.draggable = mergeOptions.draggable;
    //   toolOptions.feature.interactive = false;
    //   toolOptions.editable = mergeOptions.editable;
    //   toolOptions.reable = mergeOptions.reable;
    //   tool.start();
    // }
    if (tool) {
      let toolOptions = tool.options;
      toolOptions.feature.interactive = false;
      tool.start();
    }

    that.tool = tool;
  }


  /**
   * 
   * @param tool 
   */
  get(tool) {
    switch (tool) {
      case 'point':
        return PointTool;
      case 'distance':
        return DistancePolylineTool;
      case 'distancePolygon':
        return DistancePolygonTool;
      case 'polyline':
        return PolylineTool;
      case "pen":
        return PenTool;
      case "circle":
        return CircleTool;
      case 'ellipse':
        return EllipseTool;
      case "rect":
        return RectTool;
      case "polygon":
        return PolygonTool;
      case 'penfill':
        return PenFillTool;
      case 'militaryArrow':
        return MilitaryArrowTool;
      case 'text':
        return TextTool;
    }
  }
  /**
   * 
   */
  cancel() {
    this.tool && this.tool.cancel();
    this.tool = null;
  }
  /**
   * 
   */
  reset() {
    this.cancel();
    this.map.dragging.enable();
    //  map.doubleClickZoom.enable();
  }
  /**
   * 
   */
  onDrawEnd(e) {
    // 
    this.next.length = 0;
    //
    let { tool } = this;
    if (tool) {
      // 
      !this.history.includes(tool) && this.history.push(tool);
      //
      let { options, marker } = tool;

      marker.__data = tool.data;//

      // 
      if (tool.options.tool !== 'distance' && tool.options.tool !== 'distancePolygon' && !options.reable) {
        setTimeout(() => {
          marker.setStyle({ interactive: true, draggable: true });
          this._check(marker);
          if ((marker instanceof L.Text) === false) this._bindText(marker, tool.options)
        }, 100);
      }
      if (options.searchable) {
        this.dmap.do('search-end', { target: this, event: e, value: this.search(e) });
      }
      //
      options.onDrawEnd && options.onDrawEnd(e);
      // 
      if (options.multi) {
        // 
        tool.cancel();
        tool = this.tool = tool.clone();
        tool.start();
      } else if (options.reable) {
        // 
        tool.cancel();
        this.tool = tool;
        tool.start();
      } else {
        // 
        this.reset();
      }
    } else {
      this.reset();
    }

  }
  /**
   * 
   */
  search(e) {
    let { marker } = e,
      result = [];
    if (!marker) {
      throw 'search error'
    }
    let { _map: map } = marker
    if (marker instanceof Circle) {
      let { _radius } = marker;
      let center = marker.getLatLng();
      map.eachLayer((layer) => {
        //
        if (layer instanceof Marker || layer instanceof VectorMarker) {
          if (map.latLngToLayerPoint(layer.getLatLng()).distanceTo(map.latLngToLayerPoint(center)) < _radius) {
            result.push(layer);
          }
        } else if (layer instanceof Polyline) {
          let latlngs = layer.getLatLngs()
          if (isArray(latlngs[0])) {//todo 
            latlngs = latlngs[0]
          }
          for (let i = 0, len = latlngs.length; i < len; i++) {
            if (map.latLngToLayerPoint(latlngs[i]).distanceTo(map.latLngToLayerPoint(center)) < _radius) {
              result.push(layer);
              break;
            }
          }
        } else if (layer instanceof Polygon) {
          let latlngs = layer.getLatLngs()
          if (isArray(latlngs[0])) {
            latlngs = latlngs[0]
          }
          for (let i = 0, len = latlngs.length; i < len; i++) {
            if (map.latLngToLayerPoint(latlngs[i]).distanceTo(map.latLngToLayerPoint(center)) < _radius) {
              result.push(layer);
              break;
            }
          }
        }
      })
    } else if (marker instanceof Polygon) {
      let { _rings } = marker;
      map.eachLayer(layer => {
        if (layer instanceof Marker || layer instanceof CircleMarker) {
          if (pointInPolygon(map.latLngToLayerPoint(layer.getLatLng()), _rings[0])) {
            result.push(layer);
          }
        } else if (layer instanceof Polyline) {
          let latlngs = layer.getLatLngs();
          if (isArray(latlngs[0])) {//todo 
            latlngs = latlngs[0]
          }
          for (let i = 0, len = latlngs.length; i < len; i++) {
            if (pointInPolygon(map.latLngToLayerPoint(latlngs[i]), _rings[0])) {
              result.push(layer);
              break;
            }
          }
        } else if (layer instanceof Polygon) {
          let latlngs = layer.getLatLngs()
          if (isArray(latlngs[0])) {
            latlngs = latlngs[0]
          }
          for (let i = 0, len = latlngs.length; i < len; i++) {
            if (pointInPolygon(map.latLngToLayerPoint(latlngs[i]), _rings[0])) {
              result.push(layer);
              break;
            }
          }
        }
      })
    }
    return result;
  }
}


