let { Layer, Popup, DomEvent } = L;
let { div: $div } = T;
//
/**
 * 
 * @param {props,el}
 */
export default L.Popup2 = Popup.extend({
	options: {
		// , Infinity 
		maxWidth: 0,
		// // 
		minWidth: 0,
		// popup
		autoPan: false,
		//
		autoPosition: true,
		//
		// keepInView: false,
		// 
		closeButton: false,
		//
		// closeOnClick: false,
		//
		autoClose: false,
		//
		// autoPanPaddingTopLeft: L.point(0, 50)
	},
	// update() {
	// 	Popup.prototype.update.call(this)
	// },
	_initLayout() {
		let that = this;
		let { options, branch } = that;
		let { leaf } = options;
		if (leaf && !branch) {
			// L.setOptions(that, { maxWidth: Infinity || 0, minWidth: leaf.minWidth || 0 });
			//
			let { className } = options;
			let prefix = 'leaflet-popup';
			let container = that._container = $div(prefix + ' ' + (className || '') + ' leaflet-zoom-animated');
			let contentNode = that._contentNode = $div('', container);//
			// 
			DomEvent.disableScrollPropagation(container);
			DomEvent.disableClickPropagation(container);
			// 
			leaf.el = $div();
			leaf.props = T.assign({}, leaf.props, {
				owner: that,					 	  // 
				marker: that._source,     // 
				data: that._source && that._source.__data // 
			})
			// __vue__
			this.branch = T.render(leaf).then(vm => {
				let el = vm.$el;
				if (options.maxWidth) {
					let style = container.style;
					style.width = el.clientWidth + 'px';
					style.height = el.clientHeight + 'px';
				}
				this.setContent(el);
				return vm;
			})
		} else {
			Popup.prototype._initLayout.call(that);
		}
	},
	/**
	 * 
	 */
	_getAnchor() {
		let point = Popup.prototype._getAnchor.call(this);
		let { _map: map, _contentNode: container, _source: marker, options } = this;
		let leaf = container.firstChild;
		// 
		// todo 
		if (options.autoPosition && options.leaf && leaf && marker && marker._latlng) {
			let { style } = leaf;
			style.left = style.top = '0px';//todo
			let contentHeight = leaf.clientHeight + 20;//20 marginBottom
			let { x, y } = map.latLngToContainerPoint(marker._latlng);
			let size = map.getSize();
			let dx = 16, dy = 0;
			//
			if (container.clientHeight) {
				// 
				if (y - contentHeight < 0) {
					dy = contentHeight;
					if (y + dy > size.y) {
						dy -= (y + dy - size.y)
					}
				}
			} else {
				// 
				if (y + contentHeight > size.y) {
					dy = -contentHeight;
					if (y + dy < 0) {
						dy -= y + dy
					}
				}
			}
			// 
			if (x + leaf.clientWidth > size.x) {
				dx = -leaf.clientWidth - dx;
			}
			// 
			if (dx || dy) {
				let { x, y } = point;
				point = [dx + x, dy + y]
			}
		}
		return point;
	}
})

/**
 * Popup
 */
Layer.include({
	bindPopup(content, options) {
		if (content instanceof Popup) {
			L.setOptions(content, options);
			this._popup = content;
			(content)._source = this;
		} else {
			if (!this._popup || options) {
				this._popup = new Popup(options, this);
			}
			this._popup.setContent(content);
		}

		if (!this._popupHandlersAdded) {
			let events = {
				click: this._openPopup,
				keypress: this._onKeyPress,
				remove: this.closePopup,
				move: this._movePopup
			}
			if (options && options.hover) events.mouseover = this._openPopup;
			this.on(events)
			this._popupHandlersAdded = true;
		}
		return this;
	},

	unbindPopup() {
		if (this._popup) {
			this.off({
				click: this._openPopup,
				keypress: this._onKeyPress,
				remove: this.closePopup,
				move: this._movePopup
			});
			this._popupHandlersAdded = false;
			this._popup = null;
		}
		return this;
	}
})


