import PolylineTool from './PolylineTool'
import PolygonTool from "./PolygonTool";

/**
 * 
 */
export class DistancePolylineTool extends PolylineTool {
  options = { feature: { color: 'red', weight: 2, showMeasurements: true } }
  /**
   * 
   */
  onMouseMove(e) {
    super.onMouseMove(e);
    this.marker.updateMeasurements();
  }
}
/**
 * 
 */
export class DistancePolygonTool extends PolygonTool {
  options = { feature: { color: 'red', fillOpacity: .5, weight: 1, showMeasurements: true } }
  /**
   * 
   */
  onMouseMove(e) {
    super.onMouseMove(e);
    this.marker.updateMeasurements();
  }
}


