/**
 * @author gyb(mocheer)
 * @email `mocheer@foxmail.com`  
 * @since 2016-11-4
 */
import lonlats from '@/utils/LonLats';
import Popup from '@/polyfill/Popup';
import VectorMarker from '@/feature/VectorMarker';
import Typhoon from "@/feature/Typhoon";
import Ellipse from '@/feature/Ellipse';
import "@/feature/Text";
import '@/feature/Heat';
import River from '@/feature/River';
import MilitaryArrow from '@/feature/MilitaryArrow';
import 'overlapping-marker-spiderfier-leaflet/build/oms';
// import { AntPath, antPath } from 'leaflet-ant-path';
//
let {
  isFunction,
  isArray,
  fmt,
  assign: $assign
} = T;
let {
  featureGroup: toGroup,
  marker: toMarker,
  polyline: toPolyline,
  polygon: toPolygon,
  circle: toCircle,
  tooltip: toTooltip,
  icon: toIcon,
  divIcon: toDivIcon,
  imageOverlay: toImageOverlay
} = L;
let toArray = data => isArray(data) ? data : [data];
let toOptions = (options, data) => isFunction(options) ? options(data) : options || {};
/**
 * 
 */
export default class FeatureLayer extends T.Class {
  dmap;
  map;
  info; // 
  options; // 
  /**
   * 
   * @param {*} options 
   */
  constructor(options) {
    super();
    this.initHook();
    // 
    this.config(options);
  }
  /**
   * 
   */
  initHook() {
    let that = this;
    that._source = [];
    ['addPoints', 'addPolylines', 'addPolygons', 'addTyphoon', 'addAlarmLines'].forEach(name => {
      this.addFnHook(name, {
        precall: function () {
          let data = {
            fn: name,
            args: arguments
          };
          that._source.push(data)
          T.defer(() => {
            this.do('feature-add', data)
          })
        }
      })
    })
    this.addFnHook('clear', {
      precall: function () {
        that._source.length = 0;
        T.defer(() => {
          that.do('feature-clear');
        })
      }
    })
  }
  /**
   * 
   * @param {*} layer 
   */
  connect(layer) {
    let {
      options,
      _source
    } = this;
    layer.options = options;
    _source.forEach(item => {
      layer[item.fn].apply(layer, item.args);
    });
    //
    this.on("feature-add", e => {
      layer[e.fn].apply(layer, e.args);
    }).on('feature-clear', () => {
      layer.clear();
    })
  }
  /**
   * 
   * @param options 
   */
  config(options) {
    this.options = options;
    this.layer = toGroup();
  }
  /**
   * 
   * @param {*} marker 
   */
  has(marker) {
    return this.layer.hasLayer(marker)
  }
  /**
   * 
   */
  getMarkers() {
    return this.layer.getLayers();
  }
  /**
   * 
   * @param {*} feature 
   */
  _add(feature) {
    this.layer.addLayer(feature);
  }
  /**
   * tooltippopuptext
   * @param feature 
   */
  add(feature, options, data) {
    if (feature) {
      feature.__data = data;
      this._add(feature);
      this._bindToolTip(feature, options, data);
      this._bindPopup(feature, options, data);
      this._bindText(feature, options, data);
      //
    }
  }
  /**
   * 
   * @param data [{coordinates:[lon,lat] or [{lon,lat}] or [{lat,lng}]
   * @param style
   */
  addPoints(data, options) {
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let feature = this.createPoint(item, featureOptions);
      this.add(feature, featureOptions, item);
      return feature;
    });
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  addClusterPoints(data, options) {
    let group = L.markerClusterGroup();
    let {
      layer,
      _bindToolTip,
      _bindPopup
    } = this;
    layer.addLayer(group);
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let marker = this.createPoint(item, featureOptions);
      if (marker) {
        marker.__data = item;
        _bindToolTip(marker, featureOptions, item);
        _bindPopup(marker, featureOptions, item);
        group.addLayer(marker);
      }
      return marker;
    });
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  addClusterPoints2(data, options) {
    let group = this.oms = this.oms || new L.OverlappingMarkerSpiderfier(this.map);
    let markers = this.addPoints(data, options)
    markers.forEach(marker => {
      group.addMarker(marker)
    })
  }
  /**
   * 
   * @param {*} data
   * @param {*} options
   */
  addPolylines(data, options) {
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let marker = this.createPolyLine(item, featureOptions);
      this.add(marker, featureOptions, item);
      marker.bringToBack(); // 
      return marker;
    });
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  addPolygons(data, options) {
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let marker = this.createPolygon(item, featureOptions);
      this.add(marker, featureOptions, item);
      marker.bringToBack(); //
      return marker;
    });
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   *  
   */
  addCircles(data, options) {
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let marker = this.createCircle(item, featureOptions);
      this.add(marker, featureOptions, item);
      marker.bringToBack() // 
    });
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  addEllipses(data, options) {
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let marker = this.createEllipse(item, featureOptions);
      this.add(marker, featureOptions, item);
      marker.bringToBack() // 
    });
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  addTexts(data, options) {
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let marker = L.text(lonlats(item.coordinates || item), item.text || item.properties.text, featureOptions.feature || featureOptions);
      marker.__data = item;
      this.layer.addLayer(marker);
    })
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  addRivers(data, options) {
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let {
        feature
      } = featureOptions;
      let {
        coordinates
      } = item;
      let deep = (coordinates[0] && isArray(coordinates[0][0])) ? 2 : 1;
      let marker = new River(lonlats(coordinates, deep), feature)
      this.layer.addLayer(marker);
    })
  }
  /**
   * ()
   * @param {*} data 
   * @param {*} options 
   */
  addImage(data, options) {
    let marker = toImageOverlay(options.url, lonlats(data.coordinates, 1), options);
    this.layer.addLayer(marker);
    return marker;
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  addMilitaryArrows(data, options) {
    return toArray(data).map(item => {
      let featureOptions = toOptions(options, item);
      let {
        feature
      } = featureOptions;
      let {
        coordinates
      } = item, deep = 1;
      if (coordinates && coordinates[0] && isArray(coordinates[0][0])) {
        deep = 2;
      }
      let marker = new MilitaryArrow(lonlats(coordinates, deep), feature)
      this.add(marker, featureOptions, item);
      marker.bringToBack(); // 
      return marker;
    });
  }
  /**
   *  
   * @param {*} data 
   * @param {*} options 
   */
  addTyphoon(data, options) {
    let typhoons = this.typhoons || (this.typhoons = {});
    let feature = typhoons[data.tfbh];
    let featureOptions = toOptions(options || this.options);
    if (!feature) {
      feature = typhoons[data.tfbh] = this.createTyphoon(data, featureOptions)
      feature.addTo(this);
    } else { // 
      feature.update(data);
    }
    return feature;
  }
  /**
   *  
   * @deprecated 
   * @param data {
   *  g24: [[105, 0], [113, 4.5], [119, 11], [119, 18], [127, 22], [127, 34]],
   *  s24: { color: '#0000FF', opacity: 1, weight: 1, dashArray: '10,3', interactive: false },
   *  g48: [[105, 0], [120, 0], [132, 15], [132, 34]],
   *  s48: { color: '#FFFF00', opacity: 1, weight: 1, dashArray: '10,3', interactive: false } 
   * }
   */
  addAlarmLines(data) {
    if (!data) return;
    let {
      layer
    } = this;
    let createPolyline = (coor, options) => toPolyline(lonlats(coor, 1), options)
    let m48 = createPolyline(data.g48, data.s48);
    let m24 = createPolyline(data.g24, data.s24);
    layer.addLayer(m48).addLayer(m24);
  }
  /**
   * 
   * @param data
   * @param options
   */
  addHeat(data, options) {
    options = toOptions(options);
    let marker = L.heat(lonlats(data, 1), options.feature);
    return this.add(marker, options, data), marker;
  }
  /**
   * todo:
   * 14326
   * @param data
   * @param options 
   */
  addVelocity(data, options) {
    return new Promise((resolve) => {
      T.require(['dmap/leaflet/plugins/velocityLayer'], ({
        default: createLayer
      }) => {
        let marker = createLayer(data, options);
        this._add(marker);
        resolve(marker);
      })
    })
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  addWindy(data, options) {
    return new Promise(resolve => {
      T.require(['dmap/leaflet/plugins/windyLayer'], ({
        default: createWindyLayer
      }) => {
        options = T.assign({
          tempOverlay: true
        }, options);
        //
        let {
          windfarm
        } = this;
        if (windfarm && windfarm.options.tempOverlay === options.tempOverlay) {
          windfarm.loadWindy(data)
        } else {
          windfarm && windfarm.removeFrom(this.layer);
          windfarm = this.windfarm = createWindyLayer(options);
          this._add(windfarm);
          windfarm.loadWindy(data);
        }
        resolve(windfarm);
      })
    })
  }
  /**
   *  shp 
   */
  addShp(url, options) {
    let layer = L.shapefile(url, options)
    this._add(layer);
    return layer;
  }
  /**
   * @see https://github.com/Flexberry/Leaflet-WFST
   * @see https://github.com/azgs/azgs-leaflet
   */
  addWFS(url, options) {
    T.require(['dmap/leaflet/plugins/wfst'], ({
      default: wfst
    }) => {
      options.url = url;
      new L.WFST(options, new L.Format.GeoJSON({
          crs: L.CRS.EPSG4326
        }))
        .addTo(this.map)
        .once('load', function () {
          map.fitBounds(wfst);
        });
    })
  }
  /**
   *  geojson   
   * @param {*} geojson 
   * @see https://leafletjs.com/reference-1.3.0.html#geojson
   */
  addGeoJSON(geojson, options) {
    let geometry = geojson.type === 'Feature' ? geojson.geometry : geojson;
    let coords = geometry ? geometry.coordinates : null;
    let type = geometry.type;
    if (!coords && !geometry) {
      return null;
    }
    options = isFunction(options) ? options : $assign({}, options, geometry.properties);
    switch (type) {
      case 'MultiPoint':
        geojson = coords.map(coordinates => {
          return {
            coordinates,
            properties: geometry.properties
          }
        })
      case 'Point':
        return this.addPoints(geojson, options);
      case 'LineString':
      case 'MultiLineString':
        // { coordinates: lonlats(coords, type === 'LineString' ? 0 : 1) }
        return this.addPolylines(geometry, options)
      case 'Polygon':
      case 'MultiPolygon':
        // { coordinates: lonlats(coords, type === 'Polygon' ? 1 : 2) }
        return this.addPolygons(geometry, options);
      case 'GeometryCollection':
        return geometry.geometries.map(data => this.addGeoJSON(data, options));
        //  geojson 
      case 'Circle':
        return this.addCircles(geometry, options);
      case 'Ellipse':
        return this.addEllipses(geometry, options);
      case 'Text':
        return this.addTexts(geometry, options);
      case 'MilitaryArrow': //
        return this.addMilitaryArrows(geometry, options);
    }
  }
  /**
   * 
   */
  removeTyphoons(ids) {
    let {
      typhoons
    } = this;
    if (typhoons) {
      ids = ids || Object.keys(typhoons);
      T.each(ids, id => {
        var typhoon = typhoons[id];
        if (typhoon) {
          typhoon.remove();
          delete typhoons[id];
        }
      })
    }
  }
  /**
   * 
   * @param {*} id
   */
  getTyphoon(id) {
    let {
      typhoons
    } = this;
    return id === void 0 ? typhoons : typhoons && typhoons[id];
  }
  /**
   * 
   * @param {*} data
   * @param {*} options 
   */
  createTyphoon(data, options) {
    return new Typhoon(data, options);
  }
  /**
   * 
   * @param {*} data
   * @param {*} options {feature:{icon,symbol,glowOptions,flowOptions}}
   */
  createPoint(data, options) {
    let {
      feature
    } = options;
    let {
      tag,
      src
    } = feature || {};
    //
    let latlng = lonlats(data.coordinates || data);
    if (latlng) {
      if (tag) {
        switch (tag) {
          case 'img':
            let anchor = feature.offset;
            if (anchor) {
              anchor = [-anchor[0], -anchor[1]];
            }
            feature.icon = toIcon($assign({
              iconUrl: src,
              iconAnchor: anchor,
              // iconSize: [38, 95],
              // popupAnchor: [-3, -76],
              // shadowUrl: 'shadow.png',
              // shadowSize: [68, 95],
              // shadowAnchor: [22, 94]
            }, feature.icon));
            break;
          case 'div':
            feature.icon = toDivIcon($assign({
              iconAnchor: anchor
            }, feature.icon))
            break;
        }
        return toMarker(latlng, feature);
      }
      return new VectorMarker(latlng, feature);
    }
  }
  /**
   * 
   * @param {*} data
   * @param {*} options 
   */
  createPolyLine(data, options) {
    let {
      feature
    } = options;
    let {
      coordinates
    } = data, deep = 1;
    //todo
    if (coordinates && coordinates[0] && isArray(coordinates[0][0])) {
      deep = 2;
    }
    return toPolyline(lonlats(coordinates, deep), feature);
  }

  /**
   * 
   * @param {*} data
   * @param {*} options 
   */
  createPolygon(data, options) {
    let {
      feature
    } = options;
    let {
      coordinates
    } = data;
    let deep = (coordinates && coordinates[0] && isArray(coordinates[0][0])) ? 2 : 1;
    // 
    if (!isArray(coordinates[0])) {
      coordinates = [coordinates];
    }
    return toPolygon(lonlats(coordinates, deep), feature);
  }
  /**
   * 
   * @param {*} data
   * @param {*} options 
   */
  createCircle(data, options) {
    return toCircle(lonlats(data.coordinates), options.feature);
  }
  /**
   * 
   * @param {*} data 
   * @param {*} options 
   */
  createEllipse(data, options) {
    return new Ellipse(lonlats(data.coordinates), options.feature);
  }
  /**
   *  tooltip 
   * @param marker 
   * @param options 
   * @param data 
   */
  _bindToolTip(marker, options, data) {
    if (options = options.tooltip) {
      let {
        text
      } = options;
      let tooltip = (data && typeof text == 'string') ? fmt(text, data.properties || data) : text;
      //
      if (tooltip) {
        // options.interactive = true;
        // tooltip = toTooltip(options, marker).setContent(tooltip);
        marker.bindTooltip(tooltip, options);
      }
    }
  }
  /**
   *  popup 
   * @param marker 
   * @param options 
   */
  _bindPopup(marker, options) {
    options = options.popup;
    if (options) {
      let popup = new Popup(options).setContent(options.text);
      marker.bindPopup(popup, options);
      // marker.bindPopup(options.text, options);
    }
  }
  /**
   * 
   */
  _bindText(marker, options) {
    options = options.text;
    if (options) {
      marker.bindText(options.text, options);
    }
  }
  /**
   * @deprecated 
   */
  updateTooltips() {
    this.map && this.map.eachLayer(layer => {
      let tooltip = layer.getTooltip();
      tooltip && tooltip.isOpen && tooltip.update()
    });
  }
  /**
   * 
   * @param hide
   */
  hideTooltip(hide) {
    if (hide === void 0) hide = true;
    this.layer.eachLayer(layer => {
      let tooltip = layer.getTooltip();
      if (tooltip) {
        layer.hideTooltip(hide);
      }
    })
  }
  /**
   * 
   */
  clear() {
    this.layer.clearLayers();
    this.typhoons = null;
    return this;
  }
  /**
   * 
   */
  suspend() {
    let {
      map,
      layer
    } = this;
    map && map.removeLayer(layer);
    this.suspended = true;
  }
  /**
   * 
   */
  resume() {
    let {
      map,
      layer
    } = this;
    map && map.addLayer(layer);
  }
  /**
   * 
   * @param dmap 
   */
  addTo(dmap) {
    //
    this.dmap = dmap;
    this.map = dmap.map;
    //
    dmap.addLayer(this);
    return this;
  }
  /**
   * 
   */
  remove() {
    this.dmap.removeLayer(this.layer);
    this.dmap = null;
    this.map = null;
    return this;
  }
  /**
   * 
   * @param types 
   * @param fn 
   * @param ctx 
   */
  on(types, fn, ctx) {
    return this.layer.on(types, fn, ctx);
  }
  /**
   * 
   * @param types 
   * @param fn 
   * @param ctx 
   */
  once(types, fn, ctx) {
    return this.layer.once(types, fn, ctx);
  }
  /**
   * 
   * @param types 
   * @param fn 
   * @param ctx  
   */
  off(types, fn, ctx) {
    return this.layer.off(types, fn, ctx);
  }
  /**
   * 
   * @param {*} types 
   * @param {*} e 
   */
  do(types, e) {
    return this.layer.fire(types, e)
  }
  /**
   *  GeoJson
   * @param precision
   */
  toGeoJSON(precision) {
    let {
      layer
    } = this;
    let jsons = [];
    layer.eachLayer(layer => {
      if (layer.toGeoJSON) {
        let json = layer.toGeoJSON(precision);
        let {
          geometry,
          properties
        } = json;
        let {
          __data: data,
          options
        } = layer;
        let tooltip = layer.getTooltip();
        let text = layer.getText();

        //
        geometry.properties = $assign(properties, geometry.properties, data && data.properties, {
          feature: options,
          tooltip: tooltip && tooltip.options,
          text: text && text.options
        });
        geometry.properties.feature.className = ''; //
        jsons.push(geometry);
      }
    });
    //
    return {
      geometries: jsons,
      type: 'GeometryCollection'
    };
    //
  }
}



