import Tool from './Tool'
import VectorMarker from '@/feature/VectorMarker'
//
/**
 * 
 */
export default class PointTool extends Tool {
  options = { feature: { src: 'circle', color: '#000', radius: 5, fillColor: '#FFF', weight: 1, fillOpacity: 1 } }
  /**
   * 
   */
  start() {
    this.initMouseDownHandler();
    //
    // this.map.once('mousedown', this.onMouseDown);
    // this.map.once('mouseup', this.onEnd);
    //
    this.map.on('mousedown', this.onMouseDown, this);
  }
  /**
   * 
   */
  cancel() {
    this.map.off('mousedown', this.onMouseDown, this);
  }
  /**
   * -start
   * @param e 
   */
  _onMouseDown(e) {
    let { latlng } = e;
    let { options, layer, marker } = this;

    if (marker) {
      marker.setLatLng(latlng);
    } else {
      this.marker = new VectorMarker(latlng, options.feature).addTo(layer);
    }
    this.onEnd();
  }
}


