import Tool from './Tool'
//
export default class PenFillTool extends Tool {
  marker
  options = { feature: { color: 'red', fillOpacity: .5, weight: 1 } }
  /**
   * 
   */
  start() {
    this.initMouseDownHandler();
    let { map } = this;
    map.on('mousedown', this.onMouseDown, this);
    //
    this.restart = true;

  }
  /**
   * 
   */
  cancel() {
    let { map } = this;
    map.off('mousedown', this.onMouseDown, this);
    map.off('mousemove', this.onMouseMove, this);
  }
  /**
   * 
   */
  _onMouseDown(e) {
    this.reset();
    //
    let { marker, options, map, layer } = this;
    let { latlng } = e;
    //
    if (!marker) {
      marker = this.marker = L.rectangle(L.latLngBounds(latlng, latlng), options.feature).addTo(layer)
      map.on('mousemove', this.onMouseMove, this);
      map.once('mouseup', this.onEnd, this);
      // map.off('mousedown', this.onMouseDown);
    }
  }
  /**
   * 
   * @param e 
   */
  onMouseMove(e) {
    this.marker.addLatLng(e.latlng)
  }
}


