import Tool from './Tool'
import MilitaryArrow from '@/feature/MilitaryArrow'
//
let { LatLng } = L;
/**
 * 
 */
export default class MilitaryArrowTool extends Tool {
  options = { feature: { fillColor: 'red', weight: 1, fillOpacity: .5, color: '#333' } }
  /**
   * 
   */
  start() {
    this.initMouseDownHandler();
    let { map } = this;
    map.on('mousedown', this.onMouseDown, this);
    this.restart = true;
  }
  /**
   * 
   */
  cancel() {
    let { map } = this;
    map.off('mousedown', this.onMouseDown, this);
    map.off('mousemove', this.onMouseMove, this);
    this.mp = null;
  }
  /**
   * 
   */
  _onMouseDown(e) {
    this.reset();
    //
    let { marker, map, layer, options } = this;
    let { feature } = options;
    let { latlng } = e;
    //
    if (!marker) {
      feature.draggable = false;
      // feature.interactive = false;
      marker = this.marker = new MilitaryArrow([latlng], feature).addTo(layer)
      map.on('mousemove', this.onMouseMove, this);
      map.once('dblclick', this.onEnd, this)
    } else {
      this.mp = null;
    }
    this.lp = e.layerPoint
  }
  /**
   * 
   * @param e 
   */
  onMouseMove(e) {
    let { marker, mp } = this,
      { latlng, layerPoint } = e,
      latlngs = marker.getLatLngs();
    if (!mp) {
      let { lp } = this;
      marker.addLatLng(latlng);
      this.mp = latlng;
    } else {
      mp.lat = latlng.lat;
      mp.lng = latlng.lng;
      // marker.getBounds().extend(mp)
      marker.redraw();
    }
  }
}


