import Tool from './Tool'
import Circle from '@/feature/Circle'
// let { Circle } = L;
/**
 * 
 */
export default class CircleTool extends Tool {
  marker
  options = { feature: { color: 'red', fillOpacity: .5, weight: 1 } }
  /**
   * 
   */
  start() {
    this.initMouseDownHandler();
    let { map } = this;
    map.once('mousedown', this.onMouseDown, this);
  }
  /**
   * 
   */
  cancel() {
    let { map } = this;
    map.off('mousedown', this.onMouseDown, this);
    map.off('mousemove', this.onMouseMove, this);
  }
  /**
   * @param e 
   */
  _onMouseDown(e) {
    let { options } = this;
    let { map, layer } = this;
    let { latlng } = e;
    this.clear();//
    //
    this.marker = new Circle(latlng, options.feature).addTo(layer)
    map.on('mousemove', this.onMouseMove, this);
    map.once('mouseup', this.onEnd, this);
  }
  /**
   * 
   * @param e 
   */
  onMouseMove(e) {
    let { marker } = this;
    let latlng = e.latlng;
    let center = marker.getLatLng();
    let r = latlng.distanceTo(center);
    marker.setRadius(r);
    marker.redraw();
  }
}



