/**
 * @author gyb(mocheer)
 * @email `mocheer@foxmail.com`  
 * @since 2017.6.15
 */
let { CircleMarker } = L;
import load from "@/utils/Loader";
/**
 * 
 */
export default L.VectorMarker = CircleMarker.extend({
  /**
   * 
   */
  options: {
    // ,
    // glow: false,
    // 
    weight: 1,
    // 
    color: '#000',
    // 
    stroke: true,
    // 
    fill: true,
    // 
    radius: 5,
    //  Math.random() * 2 * Math.PI  
    angle: 0,
    // 
    riseOnHover: true
  },
  initialize: function (latlng, options) {
    CircleMarker.prototype.initialize.call(this, latlng, options);
    this._radiusY = this.options.radiusY;
  },
  // _updateBounds: function () {
  //   var r = this._radius,
  //     r2 = this._radiusY || r,
  //     w = this._clickTolerance() ,
  //     p = [r + w, r2 + w];
  //   this._pxBounds = new L.Bounds(this._point.subtract(p), this._point.add(p));
  // },
  // _empty: function () {
  //   return this._radius && !this._renderer._bounds.intersects(this._pxBounds);
  // },
  // 
  // _containsPoint: function (p) {
  //   return p.distanceTo(this._point) <= this._radius + this._clickTolerance();
  // },

  _clickTolerance: function () {
    return CircleMarker.prototype._clickTolerance.call(this) + 1;
  },
  /**
   * @override
   */
  _project() {
    this._point = this._map.latLngToLayerPoint(this._latlng);
    let { options } = this;
    let { offset, icon } = options;
    // 
    if (offset) {
      this._point = this._point.add(offset);
    }
    // 
    if (icon && icon.iconAnchor) {
      this._point = this._point.subtract(icon.iconAnchor);
    }
    this._updateBounds();
  },
  /**
   * @override
   */
  beforeAdd(map) {
    let { options } = this;
    CircleMarker.prototype.beforeAdd.call(this, map);
    //  renderer is canvas,canvas;
    if (!map.options.preferCanvas && !options.renderer) {
      options.renderer = map.__canvas = map.__canvas || L.canvas();
    }
    // 
    this.on('transformed', e => {
      options.angle = e.rotation;
      this.redraw();
    })
  },
  /**
   * @override
   * todo 
   */
  _updatePath: function () {
    let that = this;
    let { options, _renderer } = that;
    let { src } = options;
    src = src || 'circle';
    switch (src) {
      case 'triangle':
        return _renderer._updateTriangle(that);
      case 'rect':
        if (!options.radiusY) {
          that._radius = that._radiusY = 8;
        }
        return _renderer._updateRect(that);
      case 'circle':
        return _renderer._updateCircle(that);
      case 'line'://
        return _renderer._updateLine(that);
      default:
        if (that._loader) {
          _renderer._updateIcon(that);
        } else {
          // todo
          if (src && src.indexOf('/') !== 0 && !src.includes('http')) {
            src = options.src = T + src;
          }
          !that.loader && (that.loader = load(src)).then(img => {
            that._loader = img;
            that._radius = img.width / 2;
            that._radiusY = img.height / 2;
            that.redraw();
          })
        }
        break;
    }
  }
})


