/**
 * @author gyb(mocheer)
 * @email `mocheer@foxmail.com`  
 * @since 2017.6.12
 */
/**
 *  @param {boolean} restart 
 */
export default class Tool {
  /**
   * 
   */
  initMouseDownHandler() {
    this.onMouseDown = this.onMouseDown || (e => {
      if (e.originalEvent.button == 0) {
        this._onMouseDown(e);
      }
    })
  }
  /**
   * 
   * @param options 
   */
  setOptions(options) {
    T.assign(this.options, options);
  }
  /**
   * 
   */
  reback() {
    let { marker, layer, history } = this;
    if (marker) {
      if (marker._defaultShape) {///
        let latlngs = marker._defaultShape();
        let latlng = latlngs.pop();
        if (latlng) {
          history.push(latlng);
          marker.setLatLngs(latlngs);
        } else {
          marker.removeFrom(layer);
          return true;
        }
      } else {
        marker.removeFrom(layer);
        return true;
      }
    }
  }
  /**
   * 
   */
  restore() {
    let { marker, layer, history } = this;
    if (marker) {
      marker.addTo(layer);
      //
      if (marker._defaultShape && history.length > 0) {///
        let latlngs = marker._defaultShape();
        let latlng = history.pop();
        latlngs.push(latlng);
        marker.setLatLngs(latlngs);
      } else {
        return true;
      }
    }
  }
  /**
   * 
   * @param e 
   */
  onEnd(e) {
    //
    this.history = [];
    //
    this.dmap.do('draw-end', { target: this, marker: this.marker });
  }
  /**
   * 
   */
  clear() {
    let { marker } = this;
    marker && marker.remove();
  }
  /**
   * 
   */
  reset() {
    if (this.restart) {
      this.clear();
      this.restart = false;
      this.marker = null;
    }
  }
  /**
   * 
   */
  cancel() {

  }
  /**
   * 
   * @param control 
   */
  static create(owner, options) {
    let tool = new this();
    tool.owner = owner;
    tool.dmap = owner.dmap;
    tool.map = owner.map;
    tool.layer = owner.layer;
    tool.data = options.data;//layeridid
    tool.setOptions(options)
    return tool;
  }
  /**
   * 
   */
  clone() {
    return this.constructor.create(this.owner, this.options)
  }
}


